.navbar-menu{
    display: flex;
    flex-flow: column;
}

.navbar-sticky{
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    width: 100%;
    background-color: white;
    height: 60px;
    padding: 10px;
    position: fixed;
}

.navbar-listitem{
    text-decoration: none;
    color: #000;
    font-size: 150%;
    min-width: 80px;
    margin-left: 3%;
    height:50%;
    margin-right: 3%;
    display: flex;
    font-weight: 300;
    align-items: center;
    justify-content: center;
    padding-left: 5px;
    padding-right: 5px;
    cursor: pointer;
}

.navbar-lastitem{
    text-decoration: none;
    color: #000;
    height:50%;
    font-size: 150%;
    min-width: 80px;
    font-weight: 300;
    margin-left: auto; 
    margin-right: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 5px;
    padding-right: 5px;
    cursor: pointer;
}

.navbar-listitem:hover, .navbar-lastitem:hover{
    background-color: #FFEE58;
}

.navbar-listitem:after, .navbar-lastitem:after{
    background-color: #FFEE58;
}

@media only screen and (max-width: 600px) {
    .navbar-sticky a{
        font-size: 100%;
    }

    .navbar-lastitem{
        margin-left: 3%;
        margin-right: 3%;
    }
}
